import React from 'react';
import {
  Carousel,
} from 'rsuite';
import { tw } from 'twind';
import {
  Card,
  CardDeck,
} from 'react-bootstrap';

import {
  Copyright,
  Header,
} from '@Components';

const Main = ({
  collapsed,
  rtl,
  image,
  handleToggleSidebar,
  handleCollapsedChange,
  handleRtlChange,
  handleImageChange,
}) => {
  // const intl = useIntl();
  return (
    <main>
      <Header
        handleToggleSidebar={handleToggleSidebar}
      />

      <div className={tw``} style={{ backgroundColor: '#FFFBF3', }}>
        <div className={tw`p-2`}>
          <Carousel
            autoplay
            style={{ 'height': '200px', }}
            shape='bar'
          >
            <img
              src="https://via.placeholder.com/800x250/8f8e94/FFFFFF?text=AD1"
            />
            <img
              src="https://via.placeholder.com/800x250/8f8e94/FFFFFF?text=AD2"
            />
            <img
              src="https://via.placeholder.com/800x250/8f8e94/FFFFFF?text=AD3"
            />
            <img
              src="https://via.placeholder.com/800x250/8f8e94/FFFFFF?text=AD4"
            />
            <img
              src="https://via.placeholder.com/800x250/8f8e94/FFFFFF?text=AD5"
            />
          </Carousel>
        </div>

        <div className={tw`p-2`}>
          <CardDeck>
            <Card>
              <Card.Img variant="top" src="https://via.placeholder.com/200x100/00AEAE/FFFFFF?text=pic1" />
              <Card.Body>
                <Card.Title>Card title</Card.Title>
                <Card.Text>
                  This is a wider card with supporting text below as a natural lead-in to
                  additional content. This content is a little bit longer.
                </Card.Text>
              </Card.Body>
              <Card.Footer>
                <small className="text-muted">Last updated 3 mins ago</small>
              </Card.Footer>
            </Card>
            <Card>
              <Card.Img variant="top" src="https://via.placeholder.com/200x100/009393/FFFFFF?text=pic2" />
              <Card.Body>
                <Card.Title>Card title</Card.Title>
                <Card.Text>
                  This card has supporting text below as a natural lead-in to additional
                  content.{' '}
                </Card.Text>
              </Card.Body>
              <Card.Footer>
                <small className="text-muted">Last updated 3 mins ago</small>
              </Card.Footer>
            </Card>
            <Card>
              <Card.Img variant="top" src="https://via.placeholder.com/200x100/007979/FFFFFF?text=pic3" />
              <Card.Body>
                <Card.Title>Card title</Card.Title>
                <Card.Text>
                  This is a wider card with supporting text below as a natural lead-in to
                  additional content. This card has even longer content than the first to
                  show that equal height action.
                </Card.Text>
              </Card.Body>
              <Card.Footer>
                <small className="text-muted">Last updated 3 mins ago</small>
              </Card.Footer>
            </Card>
          </CardDeck>
        </div>
      </div>

      <Copyright />
    </main>
  );
};

export default Main;